export default {
	data() {
		return {
			msg:"世界 你好呀",
			flag:true
		}
	},
	methods: {
		clickHanle(num,e){
			console.log("点击了" + num,e)
		},
		setStorage(){
			uni.setStorage({
				key:'id',
				// 数据为data
				data:this.msg+80,
				success() {
					console.log("存储成功")
				}
			})
		},
		getStorage(){
			uni.getStorage({
				key:'id',
				success:(res)=>{
					// (res) {
						// console.log("获取数据成功" , res)
						console.log("获取数据成功" , res.data)
						this.msg = res.data
						// console.log("***"+this.msg)
					// }
				}
			})
		},
		removeId(){
			uni.removeStorage({
				key:'id',
				success() {
					console.log("删除数据成功" )
				}
			})
		},
		toImage(){
			uni.navigateTo({
				url: '../imageP/imageP',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	
}
